html {
  box-sizing: border-box;
}

* ::before,
::after {
  box-sizing: inherit;
}

p,
span,
h1,
h2,
h3 {
  margin: 0;
  padding: 0;
}

body {
  background-color: #d1e7e0;
  color: #161718;
  font-family: "Montserrat", Arial, "sans-serif";
  font-size: 16px;
  line-height: 1.5;
}

img {
  max-width: 100%;
}

a {
  color: #463558;
  font-weight: 500;
}

.container {
  padding-inline: 20px;
}
@media screen and (min-width: 1100px) {
  .container {
    padding-inline: 240px;
  }
}

.navigation {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 20px;
}
@media screen and (min-width: 1100px) {
  .navigation {
    margin: 50px 50px 70px;
  }
}

.navigation__logo {
  font-size: 20px;
  font-weight: 700;
  color: #463558;
  text-decoration: none;
}
@media screen and (min-width: 1100px) {
  .navigation__logo {
    font-size: 40px;
    font-weight: 600;
  }
}

.navigation__items {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 7px;
}
@media screen and (min-width: 1100px) {
  .navigation__items {
    flex-direction: row;
    gap: 10px;
  }
}

.navigation__item {
  color: #463558;
  font-size: 12px;
  font-weight: 700;
  text-decoration: none;
  padding: 5px 10px;
}
.navigation__item:hover {
  text-decoration: underline;
}
@media screen and (min-width: 1100px) {
  .navigation__item {
    font-size: 28px;
    font-weight: 600;
  }
}

.about {
  text-align: center;
  padding-top: 30px;
  padding-bottom: 20px;
}
@media screen and (min-width: 1100px) {
  .about {
    padding: 50px;
  }
}

.about__headline {
  font-size: 28px;
  color: #161718;
  font-weight: 600;
  text-align: center;
  padding-bottom: 15px;
}
@media screen and (min-width: 1100px) {
  .about__headline {
    padding-bottom: 50px;
  }
}

.about__paragraph {
  padding-bottom: 13px;
  text-align: center;
}
@media screen and (min-width: 1100px) {
  .about__paragraph {
    font-size: 18px;
  }
}

.about__image {
  display: flex;
  justify-content: center;
  padding-bottom: 30px;
}

.hero {
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("/images/nep-hero.jpg");
  height: 40vh;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  margin-inline: auto;
}
.hero--organizace {
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("/images/org-hero.jpg");
}
.hero--mexiko {
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("/images/mex-hero.jpg");
}
@media screen and (min-width: 655px) {
  .hero {
    height: 60vh;
    max-width: 1000px;
  }
}

.hero--headline {
  font-size: 40px;
  color: #161718;
  font-weight: 600;
  color: #ffffff;
  padding: 50px 0 0 40px;
}

.intro {
  display: flex;
  flex-direction: column;
}
.intro__background {
  background-color: #463558;
  color: #ffffff;
}
@media screen and (min-width: 1100px) {
  .intro {
    flex-direction: row;
  }
}

.intro__image,
.intro__text {
  flex-basis: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media screen and (min-width: 1100px) {
  .intro__image,
  .intro__text {
    width: 50%;
  }
}

.intro__text {
  background-color: #463558;
  color: #ffffff;
  font-weight: 500;
  gap: 23px;
  padding: 30px;
  text-align: center;
}
@media screen and (min-width: 1100px) {
  .intro__text {
    text-align: left;
  }
}

h2 {
  font-size: 28px;
  color: #161718;
  font-weight: 600;
  padding: 30px;
  text-align: center;
}
@media screen and (min-width: 1100px) {
  h2 {
    padding: 80px 40px 40px;
    font-size: 32px;
  }
}

h3 {
  font-size: 22px;
  color: #161718;
  font-weight: 600;
  text-align: center;
  padding-bottom: 5px;
  color: #463558;
}
@media screen and (min-width: 1100px) {
  h3 {
    font-size: 22px;
    padding-bottom: 15px;
  }
}

.columns {
  display: flex;
  flex-direction: column;
  gap: 30px;
}
.columns__icon {
  width: 20px;
  height: auto;
}
.columns__column {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 23px;
  text-align: center;
}
.columns__column--footer {
  background-color: #463558;
  color: #ffffff;
  padding: 20px;
  margin-bottom: 0;
}
@media screen and (min-width: 1100px) {
  .columns__column--footer {
    flex-direction: row;
    gap: 40px;
    align-items: center;
  }
}
.columns__column--margin {
  margin-bottom: 50px;
}
@media screen and (min-width: 1100px) {
  .columns__column--margin {
    margin-inline: 70px;
  }
}
.columns__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
}
@media screen and (min-width: 1100px) {
  .columns__item {
    flex-direction: row;
    justify-content: flex-start;
    text-align: left;
  }
}
@media screen and (min-width: 1100px) {
  .columns {
    flex-direction: row;
    gap: 20px;
  }
}

.buttons {
  display: flex;
  justify-content: center;
}

.buttons__button {
  padding: 10px;
  text-align: center;
  background-color: #463558;
  color: #ffffff;
  display: inline-block;
  border-radius: 25px;
  text-decoration: none;
  font-weight: 500;
  width: 200px;
}
.buttons__button:hover {
  color: #463558;
  background-color: #ffffff;
  border: 2px solid #463558;
}
.buttons__button--margin {
  margin: 10px 0 30px;
}
@media screen and (min-width: 1100px) {
  .buttons__button--margin {
    margin: 30px 0 50px;
  }
}

.grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
  margin: 20px 0 50px;
  justify-items: center;
}
.grid__head {
  padding-bottom: 15px;
}
.grid__text {
  height: 200px;
  background-color: #463558;
  padding: 25px;
  border-radius: 25px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  color: #ffffff;
}
.grid__item--img {
  display: flex;
  justify-content: center;
}
.grid__padding-bottom {
  padding-bottom: 15px;
}

@media screen and (min-width: 1300px) {
  .grid {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
.grid__list {
  text-align: left;
}

.link--white {
  color: #ffffff;
}